    chart;
    renderChart(){
        const heart = this.singleDevice.heart;
        let arr = []
        for(var i = 0; i < heart.length;i++){
            const obj = {
                date:heart[i].created,
                '':heart[i].temperature,
                '':heart[i].beam
            }
            arr.push(obj)
        }
        this.chart = new G2.Chart({
          container: 'chart',
          forceFit: true,
          height: 500,
          padding:'auto'
        });
        this.chart.source(arr, {
          beam: {
            min: 0,
            tickInterval: 50,
            alias: ''
          },
          temperature: {
            // min: 5,
            tickInterval: 5,
            alias: ''
          }
        });
        //  Y 
        this.chart.axis('beam', {
          label: {
            formatter: val => {
              return val + ' mm'; // 
            },
            textStyle: {
              fill: '#95ceff'
            }
          },
          line: null,
          tickLine: null
        });
        //  Y 
        this.chart.axis('temperature', {
          line: null,
          tickLine: null,
          label: {
            formatter: val => {
              return val + ' C'; // 
            },
            textStyle: {
              fill: '#90ed7d'
            }
          }
        });
        this.chart.legend({
          position: 'top'
        });
        this.chart.tooltip({
          crosshairs: {
            type: 'line'
          }
        });
        this.chart.line().position('date*').color('blue').size(2); // 
        this.chart.line().position('date*').color('red').size(2).shape('smooth'); // 
        // this.chart.point().position('date*temperature').color('#90ed7d').shape('diamond');
        this.chart.render();

    }
