import { Component, OnInit, ViewChild, OnChanges } from '@angular/core';
import { NzMessageService , zh_CN } from 'ng-zorro-antd';
import { DeviceService } from '../../services/device.service';
import { ChooseAddressComponent } from '../../directive/choose-address/choose-address.component';
import { Observable, of } from 'rxjs';
import { delay } from 'rxjs/operators'
import { Router, ActivatedRoute } from '@angular/router';
declare var AMap: any;
@Component({
  selector: 'app-device-add',
  templateUrl: './device-add.component.html',
  styleUrls: ['./device-add.component.css']
})
export class DeviceAddComponent implements OnInit, OnChanges {
  @ViewChild(ChooseAddressComponent)
  choose_address: ChooseAddressComponent
  isVisible = false
  bodyStyle = {'width':'1000px','height':'660px'};
  lng;
  lat;
  longitude;
  info = {
      type:'1',
      device_remarks:'',
      longitude:'',
      provider:''
  }
  station;
  constructor(private deviceService: DeviceService, 
              private message: NzMessageService, 
              private router: Router,
              private routerInfo: ActivatedRoute) { }

  ngOnInit() {
    this.searchType();
    Object.defineProperty({},'station_id',{
        get:function(){
            console.log(this._station_id)
            return this._station_id
        },
        set:function(newValue){
            console.log(newValue)
            this._station_id = newValue
        }
    })
  }
  ngOnChanges(value){
    console.log(value)
  }
  searchType(){
    // 
    const type = this.routerInfo.snapshot.params.type
    switch (type) {
      case "1":
        this.info.type = String(type);
        this.info.device_remarks = '';
        break;
      case "2":
        this.info.type = String(type);
        this.info.device_remarks = 'Nb-IoT';
        break;
      case "3":
        this.info.type = String(type);
        this.info.device_remarks = "";
        break;
      case "4":
        this.info.type = String(type);
        this.info.device_remarks = "";
        break;
      case "5":
        this.info.type = String(type);
        this.info.device_remarks = "";
        break;
    }
  }
  getMap() {
    const that = this
    this.isVisible = true
    const map = new AMap.Map("container", {
        resizeEnable: true,
        zoom: 12, //
        center: [121.498586, 31.239637], //
    });
    //click
    const clickEventListener = map.on('click', function(e) {
        const lng = e.lnglat.lng,
              lat = e.lnglat.lat
        console.log(lng + ':' + lat)
        that.lng = lng
        that.lat = lat
        that.longitude = lng + ',' + lat
    });
    function select(e) {
        if (e.poi && e.poi.location) {
            map.setZoom(15);
            map.setCenter(e.poi.location);
        }
    }
  }
  handleOk() {
      this.isVisible = false
      this.info.longitude = this.longitude;
  }
  handleCancel() {
      this.isVisible = false
  }
  submit(form){
      console.log(form.value.station_id)
      return false;
      if(form.value.device_name === ''){
          this.message.warning('')
          return false
      }
      if(form.value.device_number === ''){
          this.message.warning('')
          return false
      }
      if(form.value.station_id == ''){
          this.message.warning('')
          return false
      }
      if(!this.choose_address.provinceOption || this.choose_address.provinceOption.name == ''){
          this.message.warning('')
          return false
      }
      if(!this.choose_address.cityOption || this.choose_address.cityOption.name == ''){
          this.message.warning('')
          return false
      }
      if(!this.choose_address.districtOption || this.choose_address.districtOption.name == ''){
          this.message.warning('')
          return false
      }
      if(form.value.address == ''){
          this.message.warning('')
          return false
      }
      if(this.info.longitude === ''){
        this.message.warning('')
        return false
      }
      const arr = this.info.longitude.split(',')
      const condition = form.value
      condition.province_id = this.choose_address.provinceOption.id;
      condition.city_id = this.choose_address.cityOption.id;
      condition.district_id = this.choose_address.districtOption.id;
      condition.province = this.choose_address.provinceOption.name;
      condition.city = this.choose_address.cityOption.name;
      condition.district = this.choose_address.districtOption.name;
      condition.longitude = arr[0];
      condition.latitude = arr[1];
      console.log(form)
      this.deviceService.add(condition).subscribe((res)=>{
          console.log(res)
          if(res['code']===0){
              this.message.success('')
              switch (this.info.type) {
                case "1":
                  this.router.navigate(['/device/smoke'])
                  break;
                case "2":
                  this.router.navigate(['/device/wellCover'])
                  break;
                case "3":
                  this.router.navigate(['/device/door'])
                  break;
                case "4":
                  this.router.navigate(['/device/lock'])
                  break;
                case "5":
                  this.router.navigate(['/device/monitor'])
                  break;
                default:
                  this.router.navigate(['/device'])
                  break;
              }
          }
      })
  }
}
